import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Row, Col, ButtonToolbar, Button, Container } from 'reactstrap';
import Slider from 'rc-slider';

import 'rc-slider/assets/index.css';
import 'rc-tooltip/assets/bootstrap.css';
import Tooltip from 'rc-tooltip';

const propTypes = {
	children: PropTypes.node,
};

const defaultProps = {};

const createSliderWithTooltip = Slider.createSliderWithTooltip;
const Range = createSliderWithTooltip(Slider.Range);
const Handle = Slider.Handle;
const handle = (props) => {
	const { value, dragging, index, ...restProps } = props;
	return (
		<Tooltip
			prefixCls="rc-slider-tooltip"
			overlay={value}
			visible={dragging}
			placement="top"
			key={index}
		>
			<Handle value={value} {...restProps} />
		</Tooltip>
	);
};

class DefaultFooter extends Component {
	render() {

		// eslint-disable-next-line
		const { children, ...attributes } = this.props;

		return (
			<React.Fragment>
				<Container>
					<Row>
						<Col md={4}>

							<Container>
								<Row>
									<Col md={4}>
										<Button color="primary" block size="sm">
											<i className="fa fa-backward fa-lg"></i>
										</Button>
									</Col>
									<Col md={4}>
										<Button color="primary" block size="sm">
											<i className="fa fa-play fa-lg"></i>
										</Button>
									</Col>
									<Col md={4}>
										<Button color="primary" block size="sm">
											<i className="fa fa-forward fa-lg"></i>
										</Button>
									</Col>
								</Row>
							</Container>

						</Col>
						<Col md={4}>
							<Slider min={0} max={180} defaultValue={20} handle={handle} />
							{/*<p>Unknown Artist - Untitled</p>*/}

						</Col>
						<Col md={4}>



							<Container>
								<Row>
									<Col md={4}>
										<Button color="primary" block size="sm">
											<i className="fa fa-heart"></i>
										</Button>
									</Col>
									<Col>
										<Button color="primary" block size="sm">
											<i className="fa fa-volume-up"></i>
										</Button>
									</Col>
									<Col md={4}>
										<Button color="primary" block size="sm">
											<i className="icon-options-vertical"></i>
										</Button>
									</Col>
								</Row>
							</Container>



						</Col>
					</Row>
				</Container>
			</React.Fragment>
		);
	}
}

DefaultFooter.propTypes = propTypes;
DefaultFooter.defaultProps = defaultProps;

export default DefaultFooter;
